.swiper-slide.trainers__slide {
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
  // min-height: 294px;
  align-items: center;

  &:hover {
    @include hover-focus {
      & h3 {
        top: 0;
        margin: 0;
        order: -1;
        position: relative;
        bottom: -4px;
      }

      & .trainers__info {
        visibility: visible;
      }
    }
  }
}

.swiper-slide.trainers__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  // min-height: 296px;
  min-height: 100%;


  @include vp-1199 {
    width: 100%;
    min-width: 100%;
    height: auto;
    height: 38.5vw;
  }

  @include vp-767 {
    width: 100%;
    height: 84vw;
  }
}

.trainers__info {
  position: absolute;
  display: flex;
  flex-direction: column;
  background: rgba(16, 38, 84, 0.89);
  min-height: 294px;
  color: $color-secondary;
  padding: 55px 46px;
  padding-right: 20px;
  padding-bottom: 40px;
  width: 100%;
  height: 100%;
  align-items: center;
  visibility: hidden;
  overflow: auto;
  text-overflow: clip;

  @include vp-1199 {
    width: 34vw;
  }

  @include vp-767 {
    padding: 48px 7px;
    padding-top: 37px;
    width: 74vw;
  }
}

.trainers__info h3 {
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  margin: 0 auto;
  visibility: visible;
  order: 1;
  margin-top: -15px;
  margin-left: -22px;
  text-align: center;
  margin-top: 24px;
  position: absolute;
  top: min(16.5vw, 230px);

  @include vp-1199 {
    margin-bottom: 0;
    top: min(28.5vw, 30vw);
  }

  @include vp-767 {
    margin: 0 auto;
    margin-bottom: 10px;
    top: min(68.5vw, 84vw);
  }
}

.trainers__info span {
  color: #b6bdcc;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  margin-bottom: 33px;
}

.trainers__info ul {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  padding-left: 25px;
  gap: 13px;

  @include vp-767 {
    padding-left: 44px;
  }
}

.swiper-slide.trainers__slide.swiper-slide-active {
  @include vp-767 {
    width: 100%;
    height: auto;
  }
}
